<template>
	<div id="app">
    <v-snackbar
    :top="true"
      v-model="snackbar"
    >
      {{errorMsg}}
    </v-snackbar>
		<div class="header">
			<img class="header_logo" src="../assets/bylogo.png" alt="">
			<p class="header_text">手机扫码登陆演示</p>
		</div>
		<div class="content">
			<div class="login_block">
				<div class="new-loginFunc">
					<div id="lbApp" @click="queryEWM" title="点击二维码登陆" v-if="isShowEWM_login" class="new-loginFuncApp qrcode-163-icon"></div>
					<div id="lbNormal" @click="currentUserLogin"  title="账号密码登陆" v-else class="new-loginFuncNormal account-163-icon"></div>
				</div>
				<h2 class="loginbox-title">{{isRegistry ? isShowEWM_login ? '账号密码登陆' : '二维码登陆' : '注册'}}</h2>
				<div v-if="isShowEWM_login">
					<v-form ref="form" v-if="isRegistry">
						<v-text-field label="手机号" required v-model="login_form.phone"></v-text-field>
						<v-text-field label="密码" type="password" v-model="login_form.password" required></v-text-field>
						<!-- <v-checkbox label="记住密码?" required></v-checkbox> -->
						<div style="textAlign:center;margin:10px 0;">
							<v-btn :loading="loadingStatus" @click="loginClick" block x-large color="red" style="background:#2196F3;" dark>登录</v-btn>
						</div>
            <a style="color:blue;cursor:pointer;" @click="isRegistry = !isRegistry">点击注册</a>
					</v-form>
          <v-form v-model="registryModel" ref="form" v-else>
						<v-text-field v-model="registryForm.phone" label="手机号" required></v-text-field>
						<v-text-field v-model="registryForm.password" label="密码" type="password" required></v-text-field>
            <v-text-field v-model="registryForm.sbh" label="纳税人识别号" required></v-text-field>
            <v-text-field v-model="registryForm.seriaNum" label="证书序列号" required></v-text-field>
						<div style="textAlign:center;">
							<v-btn :loading="loadingStatus" @click="registryClick" block x-large color="red" style="background:#2196F3;" dark>注册</v-btn>
						</div>
            <a style="color:blue;cursor:pointer;" @click="isRegistry = !isRegistry">点击登陆</a>
					</v-form>
				</div>
				<div v-else style="width: 270px;margin: 0 auto;position:relative;">
					<img class="ewmImg" :src="img64Str" alt="">
          <div class="ewm_dialog" v-if="timer == 0" @click="queryEWM">重新获取</div>
          <p style="textAlign:center;">{{ timer != 61 ? '二维码有效时间：'+ timer + '秒' : ''}}</p>
				</div>
			</div>  
		</div>
	</div>
</template>
<script>
import axios from 'axios';
import {baseUrl,USER_INFO} from '../index'
export default {
	data(){
		return{
      loadingStatus:false,
      snackbar:false,
      isRegistry:true,
      isShowEWM_login:true,
      url:baseUrl+'/MobileDemo/mobile/server.do',
      authenticationResultUrl:baseUrl+'/MobileDemo/mobile/authenticationResult.do',
      img64Str:'',
      timer:61,
      nsrsbhStr:'',
      timer2:'',
      interTimer:'',
      registryModel:true,
      registryForm:{},
      loginUrl:baseUrl+'/MobileDemo/user/login.do',
      registryUrl:baseUrl+'/MobileDemo/user/register.do',
      login_form:{},
      errorMsg:''
		}
  },
  methods:{
    registryClick(){
      let sendData = {
        phone:this.registryForm.phone,
        password:this.registryForm.password,
        sbh:this.registryForm.sbh,
        serialNum:this.registryForm.seriaNum,
      }
      this.loadingStatus = true;
      axios({
        method:'post',
        url:this.registryUrl,
        data:sendData
      }).then(data =>{
        this.loadingStatus = false;
        this.snackbar = true;
        if(data.data.code != 0){
           this.errorMsg = data.data.msg;
        }else{
          this.errorMsg = '注册成功';
        }
      }).catch(err =>{
         this.loadingStatus = false;
         this.snackbar = true;
        this.errorMsg = '访问服务器失败';
      })
    },
    loginClick(){
      this.loadingStatus = true;
      let sendData = {
        phone:this.login_form.phone,
        password:this.login_form.password
      }
        axios({
        method:'post',
        url:this.loginUrl,
        data:sendData
      }).then(data =>{
        this.loadingStatus = false;
        if(data.data.code != 0){  //后台错误
          this.snackbar = true;
           this.errorMsg = data.data.msg;
        }else{ //登陆成功
          console.log('data',data)
          window.sessionStorage.setItem(USER_INFO,'data');
          this.toHome();
        }
      }).catch(err =>{  /// 没网
          this.loadingStatus = false;
          this.snackbar = true;
          this.errorMsg = '访问服务器失败';
      })
    },
    toHome(){
      this.$router.push('home');
    },
    currentUserLogin(){
      this.isShowEWM_login = true;
      clearInterval(this.interTimer);
      clearInterval(this.timer2);
      this.nsrsbhStr = '';
    },
    queryEWM(){
      // if(this.nsrsbhStr == '' || this.nsrsbhStr == null){
      //   this.nsrsbhStr = window.prompt('请输入纳税人识别号');
      //   if(!this.nsrsbhStr) return;
      // }
      // let timer2;  
      // window.confirm
      this.isShowEWM_login = false;
      axios({
        responseType:'arraybuffer',
        method:'get',
        url:this.url,
        params:{
          signType:0, 
        //  91640100MA75X7MM36
        //  userId:this.nsrsbhStr,
        }
      }).then(data =>{
        console.log('data',data)
        this.img64Str = 'data:image/png;base64,' + btoa(new Uint8Array(data.data).reduce((data, byte) => data + String.fromCharCode(byte), ''));
        this.timer = 60;
        this.interTimer = setInterval(() =>{
          if(this.timer == 0){
            clearInterval(this.interTimer);
            clearInterval(this.timer2);
            return;
          }
          this.timer -= 1;
        },1000);

        this.timer2 = setInterval(() =>{
          axios({
            method:'post',
            url:this.authenticationResultUrl,
            dataType:"json",
            data:{},
            params:{
              signType:0
            }
          }).then(data =>{
            console.log('data',data)
            if(data.data.msg != 'error'){
              window.sessionStorage.setItem(USER_INFO,data.data.certSn)
              this.toHome();
              clearInterval(this.timer2);
            }
          })
        },1000)
      })
    }
  }
}
</script>

