<template>
	<div>
    <v-dialog
      v-model="dialog_loading"
      hide-overlay
      persistent
      width="300">
      <v-card
        color="primary"
        dark>
        <v-card-text>
          正在获取二维码...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
		<v-card light class="mx-auto" max-width="500" outlined style="padding:20px;marginTop:50px;">
			<v-form ref="form" lazy-validation>
				<v-text-field v-model="iptVal" :rules="nameRules" label="请输入需要签名的数据"
        required
        @keyup.enter.native="signClick"></v-text-field>
				<v-btn color="error" class="mr-4" @click="signClick">
					点击签名
				</v-btn>
			</v-form>
		</v-card>
		 <v-dialog
      v-model="dialog"
      persistent
      width="500"
    >
      <v-card light>
        <v-card-title class="headline grey lighten-2">
          {{signResult ? '签名成功' : '请扫描下方二维码'}}
        </v-card-title>
      
        <v-card-text>
            <div v-if="!signResult" style="width: 270px;margin: 21px auto 0;position:relative;">
              <img class="ewmImg" :src="img64Str" alt="">
              <div class="ewm_dialog" v-if="timer == 0" @click="signClick">重新获取</div>
              <p style="textAlign:center;">{{ timer != 61 ? '二维码有效时间：'+ timer + '秒' : ''}}</p>
				  </div>
            <v-textarea
            v-else
          outlined
          style="marginTop:53px"
          name="input-7-4"
          label="签名结果"
          :value="signResult"
          :readonly="true"
        ></v-textarea>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="closeModal"
          >
            关闭
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
	</div>
</template>

<script>
import {baseUrl} from '../index'
import axios from 'axios';
export default {
	name: "Smqm",
	methods:{
    closeModal(){
      this.dialog = false;
      clearInterval(this.Intertimer);
      clearInterval(this.Intertimer2);
    },
		signClick(){
      let status = this.$refs.form.validate();
      if(!status) return;
      this.signResult = '';
      this.dialog_loading = true;
       axios({
        method:'post',
        url:this.quueryUserIdUrl,
        data:{}
      }).then(data =>{
          axios({
          responseType:'arraybuffer',
          method:'get',
          url:this.url,
          params:{
            signType:1,
            filePath:this.iptVal,
            userId:data.data.userId
          }
        }).then(data =>{
        this.img64Str = 'data:image/png;base64,' + btoa(new Uint8Array(data.data).reduce((data, byte) => data + String.fromCharCode(byte), ''));
        this.dialog_loading = false;
        this.dialog = true;
        this.timer = 60;
        this.Intertimer = setInterval(() =>{
          if(this.timer == 0){
            clearInterval(this.Intertimer);
            clearInterval(this.Intertimer2);
            return;
          }
           this.timer -= 1;
        },1000);

       
        this.Intertimer2 = setInterval(() =>{
          axios({
            method:'post',
            url:this.authenticationResultUrl,
            dataType:"json",
            data:{},
            params:{
              signType:1
            }
          }).then(data =>{
            if(data.data.msg == 'success'){
              clearInterval(this.Intertimer2);
              clearInterval(this.Intertimer);
              this.signResult = data.data.certsnResult;
            }else{

            }
          })
        },1000)
      })
      })
		}
	},
	data: () => ({
    url:baseUrl+'/MobileDemo/mobile/server.do',
    dialog_loading:false,
		iptVal: "",
		dialog:false,
    img64Str:'',
    timer:61,
    Intertimer:'',
    nameRules: [
      v => !!v || '数据不能为空'
    ],
    Intertimer2:'',
    authenticationResultUrl:baseUrl+'/MobileDemo/mobile/authenticationResult.do',
    quueryUserIdUrl:baseUrl+'/MobileDemo/mobile/getNum.do',
    signResult:''
	})
};
</script>
<style lang="less" scoped>
.ewm_dialog{
    max-width: 100%;
    height: auto;
    padding: 4px;
    line-height: 1.42857143;
    background-color: #b7b7b7ad;
    border: 1px solid #ddd;
    position: absolute;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
    top: 0;
    width: 270px;
    line-height: 261px;
    text-align: center;  
    cursor: pointer;
    color: #000;
    font-size: 30px;
}
.v-application .grey.lighten-2{
  background: #fff!important;
}
</style>