<template>
	<div>
		<v-snackbar
			light
			:top="true"
				v-model="snackbar"
			>
			{{errorMsg}}
		</v-snackbar>
		<div v-if="loading" class="loading_wrap">
			<v-progress-circular
			:size="50"
			color="primary"
			indeterminate
			></v-progress-circular>
			<div :style="{ margin: '10px 0' ,color:'#000'}">获取用户信息中...</div>
		</div>
		<v-container v-else fluid >
			<v-row>
			<v-col cols="8">
				<v-text-field light shaped outlined label="手机号" :value="userInfo.phone" readonly></v-text-field>
			</v-col>
			</v-row>
			<v-row>
			<v-col cols="8">
				<v-text-field
					light
					shaped outlined
					readonly
					label="统一社会信号代码"
					:value="userInfo.user_id"
				></v-text-field>
			</v-col>
			</v-row>
			<v-row>
			<v-col cols="8">
				<v-text-field
					light
					shaped outlined
					readonly
					label="证书序列号"
					:value="userInfo.cert_sn"
				></v-text-field>
			</v-col>
			</v-row>
		</v-container>
	</div>
</template>

<script>
import axios from "axios";
import { baseUrl, USER_INFO } from "../index";
import qs from 'qs'
export default {
	data: () => ({
		userInfo: {
		},
		errorMsg:'',
		snackbar:false,
		loading: true,
		info_url:'/MobileDemo/user/get/userInfo.do'
	}),
	async mounted() {
		let userInfo = window.sessionStorage.getItem(USER_INFO);
		try {
			let data = await axios.post(baseUrl+this.info_url,qs.stringify({certSn:userInfo}));
			if(data.data.code != 0){
				this.snackbar = true;
				this.errorMsg = data.data.msg;
			}else{
				this.userInfo = data.data.data;
			}
		} catch (err) {
			this.snackbar = true;
			this.errorMsg = '访问服务器失败';
		}
		this.loading = false;
	},
	methods: {},
};
</script>

<style>
.loading_wrap {
  margin-top: 100px;
}
.col-8{
	margin: 0 auto;
}
</style>